<template>
  <v-app>
        <div class="flex items-center justify-center">
          <v-card width="400">
            <v-card-title>
              <h1 class="display-1">Login</h1>
            </v-card-title>
            <v-card-text>
              <v-form>
                <v-text-field
                  v-model="userId"  
                  label="Username"
                  prepend-icon="mdi-account-circle"
                >
                </v-text-field>
                <v-text-field
                  :type="showPassword ? 'text' : 'password'"
                  label="Password"
                  prepend-icon="mdi-lock"
                  :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                  v-model="pw"
                  @click:append="showPassword = !showPassword"
                >
                </v-text-field>
              </v-form>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-btn color="success" @click="signUp">Register</v-btn>
              <v-spacer></v-spacer>
              <v-btn color="info" @click="logIn">Login</v-btn>
            </v-card-actions>
          </v-card>
        </div>
  </v-app>
</template>

<script>
  export default {
    name: 'Home',
    components: {      
    },
    data(){
      return{
        userId:'',
        pw:'',
      }
    },
    methods:{
      signUp(){        
        this.$store.dispatch("signUp",{
          userId: this.userId,
          pw: this.pw
        })        
      },
      logIn(){
        this.$store.dispatch("logIn",{
          userId: this.userId,
          pw: this.pw
        })        
      },
    },
  }
</script>
